.mypage-fc-download-area {
    background-color: #efebeb;
    border-top: 13px solid #ffffff;
    padding: 32px 22px 63px;

    &.modi-download-page{
        @include mq('md'){
            padding: 44px 67px 68px 67px;
        }

        @include mq('down-md'){
            padding: 20px 23px;
        }
    }

    .album-header {
        font-size: 18px;
        letter-spacing: 1.08px;
        color: #010002;
    }

    .album-download-list {
        @include mq('md'){
            display: flex;
            flex-wrap: wrap;
        }

        .inner-list{
            margin-bottom: 35px;

            @include mq('md'){
                width: 201px;

                &:not(:nth-child(4n)){
                    margin-right: 45px;
                }
            }

            @include mq('down-md'){
                margin-bottom: 35px;
                width: fit-content;
                margin: 0 auto 35px;
            }

            .download-image-wrapper{
                position: relative;
                margin-bottom: 7px;

                .badge-image{
                    position: absolute;
                    left: 14px;
                    top: 13px;
                    z-index: 10;
                    width: 26px;
                }

                .album-link{
                    position: relative;
                    display: block;

                    @include mq('md'){
                        &:hover{
                            .text-content{
                                display: flex;
                            }
                        }
                    }

                    .download-image{

                    }

                    .text-content{
                        display: none;
                        position: absolute;
                        left: 0;
                        right: 0;
                        top: 0;
                        bottom: 0;
                        flex-direction: column;
                        justify-content: flex-end;
                        background-color: rgba(0,0,0,0.7);
                        color: #fff;
                        line-height: 1.8;
                        padding: 10px;
                    }
                }

                .trash-button{
                    position: absolute;
                    right: 34px;
                    bottom: 8px;
                    z-index: 10;
                    box-sizing: border-box;
                    border-radius: 50%;
                    background-color: #fff;
                    line-height: 9px;
                    padding: 4px 5px 5px 6px;
                    width: 20px;
                    height: 20px;
                }

                .edit-button{
                    position: absolute;
                    right: 8px;
                    bottom: 8px;
                    z-index: 10;
                    box-sizing: border-box;
                    border-radius: 50%;
                    background-color: #fff;
                    line-height: 9px;
                    padding: 5px;
                    width: 20px;
                    height: 20px;
                }
            }

            .download-title {
                font-size: 14px;
                letter-spacing: 0.84px;
                color: #010002;
            }

            .download-like {
                font-family: Roboto;
                font-size: 10px;
                font-weight: 100;
                font-style: italic;
                letter-spacing: 0.6px;
                color: #010002;
                margin-top: 10px;
                margin-bottom: 5px;

                .like-icon {
                    margin-right: 7px;
                    width: 11px;
                }
            }

            .download-limited {
                font-size: 10px;
                line-height: 1.85;
                letter-spacing: 0.6px;
                color: #4d4d4d;

                .eye-icon {
                    margin-right: 7px;
                    width: 11px;
                }
            }
        }
    }
}