.mypage-album-add-area {
    background-color: #efebeb;

    @include mq('md'){
        display: flex;
        align-items: flex-start;
        padding: 40px 27px;
    }

    @include mq('down-md'){
        padding: 20px 23px 30px;
    }

    .album-add-image {
        min-width: 308px;

        @include mq('md'){
            margin-right: 35px;
        }

        @include mq('down-md'){
            margin: 0 auto;
        }

        .hide-file-name{
            display: none;
        }

        .add-image-label{
            display: block;
            position: relative;
            border-radius: 10px;
            background-color: #fff;
            height: 413px;

            &:before{
                content: '+';
                position: absolute;
                left: 0;
                right: 0;
                top: -20px;
                bottom: 0;
                text-align: center;
                background-color: #ccc;
                color: #fff;
                font-size: 45px;
                font-weight: bold;
                margin: auto;
                border-radius: 50%;
                line-height: 55px;
                width: 53px;
                height: 53px;
            }

            &:after{
                content: '';
                position: absolute;
                left: 8px;
                right: 8px;
                top: 10px;
                bottom: 10px;
                margin: auto;
                border-radius: 10px;
                border: 3px dashed #ccc;
            }

            &.modi-black{
                background-color: #000;

                &:before{
                    background-color: transparent;
                    border: 1px solid #ccc;
                    color: #cbcbcb;
                }

                &:after{
                    border-color: #cbcbcb;
                }

                .add-message{
                    color: #cbcbcb;
                }

                .caution-message{
                    color: #cbcbcb;
                }
            }

            .add-message{
                position: absolute;
                left: 0;
                right: 0;
                top: 240px;
                text-align: center;
                font-size: 13px;
                line-height: 1.46;
                letter-spacing: 1.56px;
                color: #4d4d4d;
                margin: 0 auto;
                width: fit-content;
            }

            .caution-message{
                position: absolute;
                left: 0;
                right: 0;
                bottom: 30px;
                font-size: 10px;
                font-weight: bold;
                letter-spacing: 0.6px;
                text-align: center;
                color: #4d4d4d;
            }
        }

        .add-movie-label{
            display: block;
            position: relative;
            border-radius: 10px;
            background-color: #fff;
            height: 185px;

            &:before{
                content: '+';
                position: absolute;
                left: 0;
                right: 0;
                top: 40px;
                bottom: 0;
                text-align: center;
                background-color: #ccc;
                color: #fff;
                font-size: 45px;
                font-weight: bold;
                margin: 0 auto;
                border-radius: 50%;
                line-height: 55px;
                width: 53px;
                height: 53px;
            }

            &:after{
                content: '';
                position: absolute;
                left: 8px;
                right: 8px;
                top: 10px;
                bottom: 10px;
                margin: auto;
                border-radius: 10px;
                border: 3px dashed #ccc;
            }

            .add-message{
                position: absolute;
                left: 0;
                right: 0;
                top: 120px;
                font-size: 13px;
                line-height: 1.46;
                letter-spacing: 1.56px;
                color: #4d4d4d;
                margin: 0 auto;
                width: fit-content;
            }

            .caution-message{
                position: absolute;
                left: 0;
                right: 0;
                bottom: 30px;
                font-size: 10px;
                font-weight: bold;
                letter-spacing: 0.6px;
                text-align: center;
                color: #4d4d4d;
            }
        }

        .link-movie-change{
            display: block;
            text-decoration: underline;
            margin: 18px auto 0;
            font-size: 14px;
            width: fit-content;
        }
    }

    .album-add-form {
        flex: 1;

        @include mq('down-md'){
            margin-top: 30px;
        }

        .photo-form-list {
            .list-inner {
                margin-bottom: 30px;

                .form-label{
                    display: block;
                    font-size: 12px;
                    font-weight: bold;
                    letter-spacing: 0.72px;
                    color: #010002;
                    margin-bottom: 5px;
                }

                .form-input-normal {
                    background-color: #fff;
                    border-radius: 5px;
                    border: none;
                    box-sizing: border-box;
                    padding: 12px 11px;
                    width: 100%;

                    @include mq('md'){
                        width: 50%;

                        &.modi-full{
                            width: 100%;
                        }
                    }
                }

                .form-select-normal{
                    padding: 9px 8px;
                    font-size: 13px;
                    min-width: 50%;
                    border: none;
                    border-radius: 5px;
                }

                .form-radio-label{
                    font-size: 12px;

                    &:not(:last-child){
                        margin-right: 10px;
                    }
                }

                .text-caution-message {
                    font-size: 9px;
                    letter-spacing: 1.08px;
                    text-align: left;
                    color: #989898;
                }

                .form-checkbox-normal {
                }
            }
        }
    }

    .area-button{
        @include mq('md'){
            display: flex;
            justify-content: space-between;
        }

        .button-normal{
            @include mq('md'){
                &:not(:last-child){
                    margin-right: 30px;
                }
            }

            @include mq('down-md'){
                &:not(:last-child){
                    margin-bottom: 20px;
                }
            }
        }
    }
}