.question-box-area {
    border-top: 1px solid #000;

    @include mq('down-md'){
        padding: 32px 0 68px;
    }

    @include mq('down-md'){
        padding: 32px 22px 68px;
    }

    .question-header {
        font-size: 18px;
        line-height: 2.22;
        letter-spacing: 1.08px;
        color: #010002;

        .question-icon {
            margin-right: 12px;
            width: 20px;
        }
    }

    .question-form-content {
        resize: none;
        box-sizing: border-box;
        background-color: transparent;
        border-radius: 5px;
        border: 1px solid #000;
        padding: 15px 13px;
        width: 100%;
    }
}