.movie-album-area {
    @include mq('down-md'){
        padding: 0 23px;
    }

    .movie-header {
        font-size: 18px;
        letter-spacing: 1.08px;
        color: #010002;

        .movie-icon {
            margin-right: 13px;
            width: 20px;
        }
    }

    .movie-list {
        @include mq('md'){
            display: flex;
        }

        .inner-list{
            @include mq('md'){
                width: 201px;

                &:not(:nth-child(4n)){
                    margin-right: 45px;
                }
            }

            &:not(:last-child){
                margin-bottom: 38px;
            }

            .movie-image {
                position: relative;
                margin-bottom: 7px;

                .movie-badge{
                    position: absolute;
                    left: 8px;
                    top: 8px;
                    width: 20px;
                }
            }

            .movie-title {
                margin-bottom: 4px;

                > a{
                    text-decoration: none;
                    font-size: 12px;
                    line-height: 1.54;
                    letter-spacing: 0.72px;
                    color: #010002;
                }
            }

            .movie-like {
                font-family: Roboto;
                font-size: 10px;
                font-weight: 100;
                font-style: italic;
                letter-spacing: 0.6px;
                color: #010002;
                margin-bottom: 6px;

                .like-icon{
                    margin-right: 7px;
                    width: 11px;
                }
            }

            .movie-limited {
                font-size: 10px;
                line-height: 1.85;
                letter-spacing: 0.6px;
                color: #4d4d4d;
            }
        }
    }
}