.pickup-area {
    @include mq('md'){
        margin: 0 auto;
        max-width: 939px;
    }

    @include mq('down-md'){
        padding: 0 23px;
    }

    .pickup-header {
        font-family: Roboto;
        font-size: 40px;
        font-weight: 100;
        font-style: italic;
        letter-spacing: 3.2px;
        color: #ff1744;
    }

    .list-pickup {
        display: flex;
        flex-wrap: wrap;
        margin-bottom: -35px;

        .inner-list{
            margin-bottom: 35px;

            @include mq('md'){
                width: 195px;

                &:not(:nth-child(4n)){
                    margin-right: 53px;
                }
            }

            @include mq('down-md'){
                width: 47%;

                &:nth-child(2n - 1){
                    margin-right: 6%;
                }
            }

            .pickup-image-wrapper {
                position: relative;
                margin-bottom: 5px;

                .favorite-button{
                    position: absolute;
                    left: 10px;
                    top: 10px;
                    z-index: 10;
                    width: 21px;
                    height: 20px;
                }

                .pickup-link{
                    display: block;
                }

                .new-icon {
                    position: absolute;
                    right: 10px;
                    top: 10px;
                    z-index: 100;
                    background-color: #ff1744;
                    color: #fff;
                    font-family: Roboto;
                    font-size: 8px;
                    font-weight: bold;
                    font-stretch: condensed;
                    letter-spacing: 0.8px;
                    border-radius: 5px;
                    padding: 4px 6px 2px;
                }
            }

            .pickup-title {
                margin-bottom: 4px;

                > a{
                    text-decoration: none;
                    font-size: 9px;
                    font-weight: bold;
                    line-height: 1.95;
                    letter-spacing: 3.4px;
                    color: #5a6c75;
                }
            }

            .pickup-description {
                margin-bottom: 6px;
                color: #010002;
            }

            .pickup-like {
                font-family: Roboto;
                font-size: 10px;
                font-weight: 100;
                font-style: italic;
                letter-spacing: 0.96px;
                color: #010002;

                .like-icon{
                    margin-right: 7px;
                    width: 11px;
                }
            }
        }
    }
}