.cart-contents-wrapper {
    background-color: #efebeb;

    @include mq('md'){
        margin: 0 auto;
        max-width: 939px;
    }

    .cart-contents-area {
        padding: 0 22px 38px;

        .buy-list {
            .list-inner {
                position: relative;
                display: flex;
                align-items: center;
                justify-content: space-between;
                border-bottom: 1px solid #010002;
                padding: 27px 0 29px;

                .cart-text {
                    font-size: 11px;
                    letter-spacing: 0.66px;
                    color: #010002;
                }

                .delete-link{
                    font-size: 13px;
                    letter-spacing: 0.65px;
                    color: #78787b;
                }
            }
        }

        .calculate_area {
            padding: 16px 0 0;

            .Individual-calculation {
                font-size: 13px;
                line-height: 2;
                letter-spacing: 0.78px;
                color: #010002;
            }

            .total-fee {
                display: flex;
                justify-content: space-between;
                margin-top: 15px;

                .item-name {
                    font-size: 17px;
                    letter-spacing: 1.02px;
                    color: #010002;
                }

                .amount-text {
                    font-size: 22px;
                    letter-spacing: 1.32px;
                    color: #010002;
                }
            }

            .cart-button{
                display: block;
                font-family: NotoSansCJKjp;
                text-decoration: none;
                background-color: #fff;
                border-radius: 30px;
                border: 1px solid #ff1744;
                text-align: center;
                font-size: 14px;
                font-weight: bold;
                line-height: 1.21;
                letter-spacing: 0.84px;
                color: #ff1744;
                margin-top: 31px;
                padding: 10px;

                .add-icon{
                    vertical-align: -2px;
                    margin-right: 11px;
                    width: 17px;
                }
            }
        }
    }

    .point-contents-area {
        padding: 30px 22px 36px;

        .available-points-area {
            .item-name {
                font-size: 13px;
                letter-spacing: 0.78px;
                color: #010002;
                margin-bottom: 12px;
            }

            .point-value {
                font-size: 18px;
                line-height: 1.19;
                color: #010002;

                .value-unit{
                    vertical-align: 2px;
                    margin-left: 15px;
                    font-size: 12px;
                    letter-spacing: 0.77px;
                    color: #010002;
                }
            }
        }

        .expected-expire {
            margin-top: 24px;

            .item-name {
                font-size: 13px;
                letter-spacing: 0.78px;
                color: #010002;
                margin-bottom: 15px;
            }

            .point-value {
                font-size: 12px;
                line-height: 2.03;
                letter-spacing: 1.2px;
                color: #78787b;
            }
        }

        .point-button{
            display: block;
            font-family: NotoSansCJKjp;
            text-decoration: none;
            background-color: #fff;
            border-radius: 30px;
            border: 1px solid #ff1744;
            text-align: center;
            font-size: 14px;
            font-weight: bold;
            line-height: 1.21;
            letter-spacing: 0.84px;
            color: #ff1744;
            margin-top: 31px;
            padding: 10px;
        }
    }
}
