.mypage-message-contents-header {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 12px 15px;
    background-color: #fff;
    border-top: 1px solid #e5e5e5;

    .user-data {
        display: flex;
        align-items: center;

        .user-icon {
            border-radius: 50%;
            margin-right: 14px;
            width: 50px;
        }

        .message-info {
            .user-name {
                font-size: 16.25px;
                letter-spacing: 0.975px;
                color: #010002;
            }

            .message-date {
                font-size: 12.2px;
                letter-spacing: 1.22px;
                color: #78787b;
            }
        }
    }

    .additional-data{
        display: flex;
        align-items: center;

        .badge-image {
            margin-right: 25px;
            width: 25px;
        }

        .back-to-button {
            text-decoration: none;

            @include mq('md'){
                display: none;
            }

            .back-icon{
                vertical-align: -4px;
                margin-right: 14px;
                width: 16px;
                height: 20px;
            }
        }
    }
}