.fanclub-content {
    border-bottom: 1px solid #000;
    padding-bottom: 89px;

    @include mq('md'){
        margin: 0 auto;
        max-width: 939px;
    }

    .fanclub-main-content{
        @include mq('md'){
            display: flex;
            justify-content: space-between;
        }

        .album-content{
            @include mq('md'){
                width: 408px;
            }

            .photo-slide-wrapper {
                position: relative;

                .photo-list {
                    position: relative;

                    .slick-prev{
                        position: absolute;
                        left: 0;
                        top: 0;
                        bottom: 0;
                        z-index: 10;
                        margin: auto;
                        background-color: rgba(0,0,0,0.3);
                        font-size: 0;
                        border: none;
                        height: 36px;

                        &:before{
                            content: url(../img/icon-arrow-prev.png);
                        }
                    }

                    .slick-next{
                        position: absolute;
                        right: 0;
                        top: 0;
                        bottom: 0;
                        z-index: 10;
                        margin: auto;
                        background-color: rgba(0,0,0,0.3);
                        font-size: 0;
                        border: none;
                        height: 36px;

                        &:before{
                            content: url(../img/icon-arrow-next.png);
                        }
                    }
                }

                .photo-index{
                    text-align: center;
                    font-weight: bold;

                    @include mq('md'){
                        font-size: 12px;
                        line-height: 1.75;
                        letter-spacing: 2.15px;
                        color: #010002;
                        margin-top: 1px;
                    }

                    @include mq('down-md'){
                        position: absolute;
                        left: 12px;
                        top: 12px;
                        background-color: rgba(0,0,0,0.3);
                        border-radius: 20px;
                        font-size: 10px;
                        letter-spacing: 1.79px;
                        color: #ffffff;
                        padding: 3px 5px 4px;
                        width: 50px;
                    }
                }
            }

            .photo-slide-thumbnail{
                display: flex;
                flex-wrap: wrap;
                justify-content: center;
                margin-top: 24px;
                margin-bottom: -19px;

                @include mq('down-md'){
                    display: none;
                }

                .inner-list{
                    width: 53px;
                    margin-bottom: 19px;

                    &:not(:nth-child(6n)){
                        margin-right: 18px;
                    }

                    img{
                        box-sizing: border-box;
                        width: 53px;
                        height: 65px;

                        &.modi-current{
                            border: 2px solid #ff1744;
                        }
                    }
                }
            }

            .button-normal{
                @include mq('down-md'){
                    display: none;
                }
            }
        }

        .right-content{
            @include mq('md'){
                width: 482px;
            }

            .content-area {
                @include mq('md'){
                    padding-bottom: 25px;
                }

                @include mq('down-md'){
                    padding: 25px 22px;
                }

                .girl-name {
                    margin-bottom: 15px;

                    > a{
                        font-family: Roboto;
                        font-size: 14px;
                        font-weight: bold;
                        font-stretch: condensed;
                        letter-spacing: 0.7px;
                    }
                }

                .room-name {
                    font-size: 16px;
                    font-weight: bold;
                    letter-spacing: 0.8px;
                    color: #010002;
                    margin-bottom: 20px;
                }

                .room-description {
                    font-size: 12px;
                    letter-spacing: 1.075px;
                    color: #010002;
                    margin-bottom: 25px;
                }

                .tag-list {
                    display: flex;
                    flex-wrap: wrap;
                    margin-bottom: 25px;

                    .inner-list {
                        > a{
                            border-radius: 3px;
                            background-color: #000;
                            font-size: 9px;
                            font-weight: bold;
                            text-decoration: none;
                            line-height: 1.94;
                            letter-spacing: 0.805px;
                            color: #fff;
                            margin-right: 15px;
                            padding: 3px 8px;

                            &.modi-new{
                                background-color: #ff1744;
                            }
                        }
                    }
                }

                .room-data {
                    font-family: NotoSansCJKjp;
                    font-size: 14px;
                    font-weight: 300;
                    letter-spacing: 0.35px;
                    color: #010002;
                    margin-bottom: 25px;
                }
            }

            .badge-area {
                border-top: 1px solid #000;

                @include mq('md'){
                    padding: 23px 0 28px;
                }

                @include mq('down-md'){
                    padding: 23px 22px 28px;
                }

                .badge-header {
                    display: flex;
                    align-items: flex-start;

                    .badge-image {
                        margin-right: 23px;
                        width: 50px;
                    }

                    .text-content {
                        .badge-heading {
                            font-weight: bold;
                            font-size: 14px;
                            letter-spacing: 0.6925px;
                            color: #010002;
                            margin-bottom: 15px;
                        }

                        .badge-description {
                            font-size: 12px;
                            line-height: 1.63;
                            letter-spacing: 0.6px;
                            color: #010002;
                            margin-bottom: 10px;
                        }

                        .badge-point {
                            font-size: 10px;
                            line-height: 2.35;
                            letter-spacing: 0.895px;
                        }

                        .badge-supporter {
                            font-size: 10px;
                            line-height: 2.35;
                            letter-spacing: 0.895px;
                        }
                    }
                }

                .cart-button {
                    display: block;
                    font-family: NotoSansCJKjp;
                    text-decoration: none;
                    border-radius: 30px;
                    border: 1px solid #ff1744;
                    text-align: center;
                    font-size: 14px;
                    font-weight: bold;
                    line-height: 1.21;
                    letter-spacing: 0.84px;
                    color: #ff1744;
                    padding: 10px;

                    @include mq('md'){
                        margin-top: 30px;
                    }

                    .add-icon{
                        vertical-align: -2px;
                        margin-right: 23px;
                        width: 17px;
                    }
                }
            }
        }
    }
}