//--------------------------------------------------------------------
//  汎用ボタン
//--------------------------------------------------------------------
.button-text{
    display: block;
    font-family: Roboto;
    text-decoration: none;
    font-size: 24px;
    font-weight: 300;
    font-stretch: condensed;
    line-height: 1.67;
    letter-spacing: 2.88px;
    color: #010002;
    margin: auto;
    border-bottom: 1px solid #808081;
    padding-bottom: 2px;
    width: fit-content;
}

.button-normal {
    appearance: none;
    display: block;
    box-sizing: border-box;
    font-family: NotoSansCJKjp;
    background-color: transparent;
    border-radius: 30px;
    font-size: 14px;
    font-weight: bold;
    text-align: center;
    text-decoration: none;
    line-height: 1.21;
    letter-spacing: 0.84px;
    color: #010002;
    margin: 0 auto;
    border: 1px solid #000;
    background-color: #fff;
    padding: 10px;
    max-width: 600px;
    width: 100%;

    &.modi-pink{
        border-color: #ff1744;
        color: #ff1744;
    }

    .add-icon {
        vertical-align: -3px;
        margin-right: 14px;
        width: 20px;
    }
}

.button-twitter {
    display: block;
    text-decoration: none;
    border-radius: 5px;
    color: #fff;
    background-color: #2cadf4;
    text-align: center;
    font-size: 17px;
    line-height: 0.97;
    letter-spacing: 1.92px;
    padding: 12px 10px 11px;

    @include mq('md'){
        width: fit-content;
        padding: 10px 40px;
    }

    .twitter-icon{
        vertical-align: -3px;
        margin-right: 7px;
        width: 20px;
    }
}
