//------------------------------------------------------------------------------------------------
//
//  Header
//
//------------------------------------------------------------------------------------------------
.l-header{
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    z-index: 500;
    margin: 0 auto;
    background-color: #fff;

    .pc-header{
        display: none;
        justify-content: space-between;
        padding: 15px 27px 15px 23px;

        @include mq('md'){
            display: flex;
        }

        .left-area{
            display: flex;
            align-items: center;
            width: 61%;
            justify-content: space-between;

            .logo-area {
                .main-logo {
                    width: 97px;
                }
            }

            .search-area {
                position: relative;
                width: 83.95%;

                .search-box {
                    box-sizing: border-box;
                    appearance: none;
                    background-color: #efebeb;
                    border: none;
                    border-radius: 24px;
                    font-size: 12px;
                    letter-spacing: 1.2px;
                    padding: 14px 21px;
                    width: 100%;

                    &::placeholder{
                        color: #78787b;
                    }
                }

                .search-button {
                    position: absolute;
                    right: 16px;
                    top: 11px;
                    appearance: none;
                    background-color: transparent;
                    border: none;
                    width: 20px;
                    height: 20px;
                    padding: 0;
                }
            }
        }

        .pc-navigation {
            display: flex;
            align-items: center;

            .login-link {
                text-decoration: none;
                font-size: 14px;
                letter-spacing: 1.4px;
                color: #010002;
                margin-right: 30px;
            }

            .menu-list {
                display: flex;

                .list-inner {
                    &:not(:last-child){
                        margin-right: 21px;
                    }

                    > a{
                        position: relative;

                        &.modi-notifi-count{
                            &:after{
                                content: attr(data-count);
                                position: absolute;
                                right: -13px;
                                top: -11px;
                                border-radius: 50%;
                                background-color: #ff1744;
                                color: #fff;
                                text-align: center;
                                line-height: 18px;
                                font-family: Roboto;
                                font-weight: bold;
                                font-size: 11px;
                                padding: 2px 0 0;
                                width: 18px;
                            }
                        }

                        .image-heart {
                            width: 22px;
                        }

                        .image-cart {
                            width: 21px;
                        }

                        .image-bell {
                            width: 20px;
                        }

                        .image-smile {
                            width: 24px;
                        }

                        .image-menu {
                            width: 24px;
                        }
                    }
                }
            }
        }
    }

    .mobile-header{
        display: none;
        justify-content: space-between;
        align-items: flex-end;
        padding: 20px 4px;

        @include mq('down-md'){
            display: flex;
        }

        .navigation-area{
            width: 38%;

            > .menu-list{
                display: flex;

                &.modi-right{
                    justify-content: flex-end;
                }

                .list-inner{
                    margin-right: 2%;
                    width: 30%;

                    > a{
                        display: block;
                        text-align: center;
                        text-decoration: none;

                        .image-menu{
                            width: 14px;
                        }

                        .image-smile{
                            width: 18px;
                        }

                        .image-login{
                            width: 20px;
                        }

                        .image-cart{
                            width: 18px;
                        }

                        .image-search{
                            width: 19px;
                        }

                        .menu-text{
                            font-family: Roboto;
                            font-size: 9px;
                            font-weight: 300;
                            text-align: center;
                            color: #010002;
                            margin-top: 5px;
                        }
                    }
                }
            }
        }

        .logo-area{
            width: 93px;
        }
    }
}