//--------------------------------------------------------------------
//  .list-photo-tile
//--------------------------------------------------------------------
.list-photo-tile {
    display: flex;
    flex-wrap: wrap;

    .list-inner {
        position: relative;

        @include mq('down-md'){
            width: 50%;
        }

        @include mq('md'){
            width: 25%;
        }

        > a{
            &:hover{
                @include mq('md'){
                    opacity: 1;

                    .text-area {
                        display: flex;
                    }
                }
            }
        }

        .favorite-icon {
            position: absolute;
            left: 10px;
            top: 10px;
            z-index: 10;
            width: 21px;
            height: 20px;
        }

        .new-icon{
            position: absolute;
            right: 10px;
            top: 10px;
            z-index: 100;
            background-color: #ff1744;
            color: #fff;
            font-family: Roboto;
            font-size: 8px;
            font-weight: bold;
            font-stretch: condensed;
            letter-spacing: 0.8px;
            border-radius: 5px;
            padding: 6px 7px 4px;
        }

        .main-photo {
            width: 100%;
        }

        .text-area {
            position: absolute;
            left: 0;
            right: 0;
            bottom: 0;
            display: block;
            background-color: rgba(0,0,0,0.7);
            padding: 12px 10px;
            color: #ffffff;

            @include mq('md'){
                display: none;
                flex-direction: column;
                justify-content: flex-end;
                top: 0;
            }

            .tile-room-name{
                font-family: Roboto;
                font-size: 20px;
                font-weight: bold;
                font-style: italic;
                color: #fff;
                margin-bottom: 10px;

                @include mq('down-md'){
                    display: none;
                }
            }

            .tile-name {
                display: block;
                font-weight: bold;
                line-height: 1.73;
                letter-spacing: 0.65px;

                @include mq('md'){
                    font-size: 16px;
                    margin-bottom: 8px;
                }

                @include mq('down-md'){
                    font-size: 13px;
                }
            }

            .tile-description {
                display: block;
                font-size: 11px;
                line-height: 2.05;
                text-decoration: underline;

                @include mq('md'){
                    font-size: 14px;
                }

                @include mq('down-md'){
                    font-size: 11px;
                }
            }
        }
    }
}

//--------------------------------------------------------------------
//  .list-article
//--------------------------------------------------------------------
.list-article{
    display: flex;
    flex-wrap: wrap;

    > .list-inner{
        margin-bottom: 48px;

        @include mq('md'){
            width: 22%;

            &:not(:last-child){
                margin-right: 4%;
            }
        }

        @include mq('down-md'){
            width: 45%;
        }

        &:nth-child(2n - 1){
            @include mq('down-md'){
                margin-right: 10%;
            }
        }

        .main-photo {
            margin-bottom: 17px;
        }

        .article-info {
            font-size: 10px;
            margin-bottom: 14px;
        }

        .article-title {
            margin-bottom: 18px;

            > a{
                font-size: 14px;
                line-height: 1.46;
            }
        }

        .article-tag {
            margin-bottom: -5px;

            > .list-inner{
                display: inline-block;
                margin-bottom: 5px;

                > a{
                    font-size: 10px;
                }
            }
        }
    }
}