.mypage-fc-info-area {
    background-color: #efebeb;
    padding: 23px 22px;

    .fc-main-image {
        display: block;
        margin: 0 auto 21px;
    }

    .open-date {
        margin-top: 40px;
        font-size: 10px;
        line-height: 2.44;
        letter-spacing: 1px;
        color: #78787b;
    }

    .fc-name {
        margin-top: 8px;
        font-size: 20px;
        letter-spacing: 1.2px;
        color: #010002;
    }

    .fc-fun-count{
        margin-top: 10px;
        font-size: 14px;
        font-weight: 300;
        line-height: 1.2;
        letter-spacing: 0.7px;
        color: #010002;
    }

    .fc-description {
        margin-top: 12px;
        font-size: 14px;
        font-weight: 300;
        line-height: 1.6;
        letter-spacing: 0.7px;
        color: #010002;
    }

    .tag-list {
        display: flex;
        flex-wrap: wrap;
        margin-top: 30px;
        margin-bottom: 35px;

        @include mq('down-md'){
            justify-content: space-between;
        }

        .inner-list {
            margin-bottom: 5px;

            > a{
                display: block;
                border-radius: 6px;
                background-color: #000;
                font-size: 15.3px;
                letter-spacing: 2.74px;
                text-align: left;
                color: #ffffff;
                text-decoration: none;
                margin-right: 15px;
                padding: 6px 15px 5px;
            }
        }
    }
}