
.top-fanclub-area {
    padding: 76px 33px 30px;
    color: #000;
    background-color: #fff;

    @include mq('md'){
        margin: 0 auto;
        max-width: 1280px;
    }

    .main-text{
        font-size: 13px;
        line-height: 2;
        letter-spacing: 2.34px;
    }

    .list-photo-tile{
        margin: 0 -33px;
    }

    .select-default-wrapper{
        @include mq('md'){
            width: fit-content;
        }
    }
}