.mypage-message-create-form {
    background-color: #efebeb;
    padding: 15px 22px 35px;

    .form-label {
        display: block;
        font-size: 12px;
        letter-spacing: 1.44px;
        margin-bottom: 10px;
    }

    .form-input {
        box-sizing: border-box;
        border-radius: 8px;
        background-color: #ffffff;
        font-size: 20px;
        border: none;
        padding: 8px;
        width: 100%;
    }

    .form-textarea {
        box-sizing: border-box;
        border: none;
        border-radius: 8px;
        background-color: #ffffff;
        color: #000;
        font-size: 16px;
        padding: 8px;
        width: 100%;
    }

    .form-text {
        font-size: 10px;
        margin-top: 12px;
    }

    .select-image {
        display: flex;
        font-size: 12px;
        letter-spacing: 1.44px;

        .form-image-select {
            display: none;
        }

        .select-image-text{
            text-decoration: underline;
            font-size: 12px;
            letter-spacing: 1.44px;

            &:before{
                content: '';
                display: inline-block;
                vertical-align: middle;
                background: url(../img/icon-album.png);
                background-size: 100% 100%;
                margin-right: 19px;
                width: 20px;
                height: 22px;
            }
        }
    }

    .form-submit-button {
        appearance: none;
        border-radius: 30px;
        background-color: #fff;
        font-size: 14px;
        letter-spacing: 1.68px;
        color: #ff1744;
        border: none;
        padding: 12px;
        width: 100%;
    }
}