.paging-area {
    display: flex;
    justify-content: center;
    align-items: center;

    .page-double-prev {
        position: relative;
        text-decoration: none;
        box-sizing: border-box;
        margin-right: 10px;
        border: 1px solid #999;
        padding: 10px;
        width: 40px;
        height: 40px;

        &:before{
            content: '';
            position: absolute;
            left: 11px;
            top: 0;
            bottom: 0;
            margin: auto;
            transform: rotate(-135deg);
            display: block;
            border: 1px solid #333;
            border-width: 2px 2px 0 0;
            width: 8px;
            height: 8px;
        }

        &:after{
            content: '';
            position: absolute;
            left: 21px;
            top: 0;
            bottom: 0;
            margin: auto;
            transform: rotate(-135deg);
            display: block;
            border: 1px solid #333;
            border-width: 2px 2px 0 0;
            width: 8px;
            height: 8px;
        }
    }

    .page-prev {
        position: relative;
        text-decoration: none;
        box-sizing: border-box;
        border: 1px solid #999;
        padding: 10px;
        width: 40px;
        height: 40px;

        &:before{
            content: '';
            position: absolute;
            left: 16px;
            top: 0;
            bottom: 0;
            margin: auto;
            transform: rotate(-135deg);
            display: block;
            border: 1px solid #333;
            border-width: 2px 2px 0 0;
            width: 8px;
            height: 8px;
        }
    }

    .page-number {
        flex: 1;
        text-align: center;
        font-family: Roboto;
        font-size: 16px;
        font-weight: 500;
        font-style: italic;
        letter-spacing: 2.8px;
        color: #010002;
        max-width: 300px;
    }

    .page-next {
        position: relative;
        text-decoration: none;
        box-sizing: border-box;
        margin-right: 10px;
        border: 1px solid #999;
        padding: 10px;
        width: 40px;
        height: 40px;

        &:before{
            content: '';
            position: absolute;
            left: 12px;
            top: 0;
            bottom: 0;
            margin: auto;
            transform: rotate(45deg);
            display: block;
            border: 1px solid #333;
            border-width: 2px 2px 0 0;
            width: 8px;
            height: 8px;
        }
    }

    .page-double-next {
        position: relative;
        text-decoration: none;
        box-sizing: border-box;
        border: 1px solid #999;
        padding: 10px;
        width: 40px;
        height: 40px;

        &:before{
            content: '';
            position: absolute;
            left: 7px;
            top: 0;
            bottom: 0;
            margin: auto;
            transform: rotate(45deg);
            display: block;
            border: 1px solid #333;
            border-width: 2px 2px 0 0;
            width: 8px;
            height: 8px;
        }

        &:after{
            content: '';
            position: absolute;
            left: 17px;
            top: 0;
            bottom: 0;
            margin: auto;
            transform: rotate(45deg);
            display: block;
            border: 1px solid #333;
            border-width: 2px 2px 0 0;
            width: 8px;
            height: 8px;
        }
    }
}