.mypage-fc-movie-area {
    background-color: #efebeb;
    border-top: 13px solid #ffffff;
    padding: 32px 22px 63px;

    &.modi-movie-page{
        @include mq('md'){
            padding: 44px 69px 68px 70px;
        }

        @include mq('down-md'){
            padding: 20px 23px;
        }
    }

    .album-header {
        font-size: 18px;
        letter-spacing: 1.08px;
        color: #010002;
    }

    .movie-list {
        @include mq('md'){
            display: flex;
            flex-wrap: wrap;
        }

        .inner-list{
            @include mq('md'){
                width: 201px;

                &:not(:nth-child(4n)){
                    margin-right: 45px;
                }
            }

            @include mq('down-md'){
                margin: 0 auto;
                width: fit-content;
            }

            &:not(:last-child){
                margin-bottom: 38px;
            }

            .movie-image {
                position: relative;
                margin-bottom: 7px;

                .movie-badge{
                    position: absolute;
                    left: 8px;
                    top: 8px;
                    width: 20px;
                }

                .trash-button{
                    position: absolute;
                    right: 34px;
                    bottom: 8px;
                    z-index: 10;
                    box-sizing: border-box;
                    border-radius: 50%;
                    background-color: #fff;
                    line-height: 9px;
                    padding: 4px 5px 5px 6px;
                    width: 20px;
                    height: 20px;
                }

                .edit-button{
                    position: absolute;
                    right: 8px;
                    bottom: 8px;
                    z-index: 10;
                    box-sizing: border-box;
                    border-radius: 50%;
                    background-color: #fff;
                    line-height: 9px;
                    padding: 5px;
                    width: 20px;
                    height: 20px;
                }
            }

            .movie-title {
                margin-bottom: 4px;

                > a{
                    text-decoration: none;
                    font-size: 12px;
                    line-height: 1.54;
                    letter-spacing: 0.72px;
                    color: #010002;
                }
            }

            .movie-like {
                font-family: Roboto;
                font-size: 10px;
                font-weight: 100;
                font-style: italic;
                letter-spacing: 0.6px;
                color: #010002;
                margin-bottom: 6px;

                .like-icon{
                    margin-right: 7px;
                    width: 11px;
                }
            }

            .movie-limited {
                font-size: 10px;
                font-weight: 100;
                font-style: italic;
                line-height: 1.85;
                letter-spacing: 0.6px;
                color: #4d4d4d;

                .eye-icon {
                    margin-right: 7px;
                    width: 11px;
                }
            }
        }
    }
}