.select-default-wrapper {
    position: relative;

    &:after{
        content: '▼';
        position: absolute;
        right: 0;
        top: 4px;
        font-size: 14px;
    }

    .select-default {
        appearance: none;
        font-size: 13px;
        font-weight: 300;
        line-height: 1.63;
        background-color: transparent;
        border: none;
        border-bottom: 1px solid #000;
        padding: 7px;
        min-width: 200px;
        width: 100%;
    }
}