.top-signup-area {
    color: #fff;
    background-color: #ff1744;

    @include mq('md'){
        display: flex;
        margin: 0 auto;
        width: 1280px;
    }

    .signup-main-image {
        @include mq('md'){
            width: 50%;
        }
    }

    .singup-area {
        @include mq('md'){
            padding: 80px 56px 44px;
            width: 50%;
        }

        @include mq('down-md'){
            padding: 25px 48px;
        }

        .signup-main-heading {
            font-family: Roboto;
            font-size: 23px;
            font-weight: 100;
            font-style: italic;
            letter-spacing: 3.76px;

            @include mq('md'){
                font-size: 40px;
            }

            @include mq('down-md'){
                font-size: 23px;
            }
        }

        .signup-sub-heading {
            font-size: 16px;
            font-weight: bold;
            line-height: 0.97;
            letter-spacing: 3.84px;
        }

        .text-caution-message {
            font-size: 9px;
        }

        .singup-form-list {
            .list-inner {
                margin-bottom: 20px;

                .form-input-normal {
                    display: block;
                    background-color: transparent;
                    font-family: Roboto;
                    font-size: 12px;
                    font-weight: bold;
                    font-stretch: condensed;
                    line-height: 1.29;
                    letter-spacing: 1.44px;
                    color: #fff;
                    border: none;
                    border-bottom: 1px solid #fff;
                    width: 100%;

                    &::placeholder {
                        color: #fff;
                    }
                }

                .form-checkbox-normal {
                    position: relative;
                    outline: none;
                    background-color: transparent;
                    border: 1px solid #fff;
                    appearance: none;
                    margin-right: 4px;
                    width: 10px;
                    height: 10px;

                    &::before{
                        content: '';
                        display: none;
                        position: absolute;
                        left: 1px;
                        top: 1px;
                        border: 1px solid #fff;
                        border-width: 1px 1px 0 0;
                        width: 5px;
                        height: 3px;
                        transform: rotate(135deg);
                    }

                    &:checked{
                        &::before{
                            display: block;
                        }
                    }
                }

                .form-checkbox-label{
                    font-size: 24px;
                    color: #ffffff;
                }
            }
        }

        .signup-submit{
            position: relative;
            display: block;
            text-align: center;
            border-radius: 12px;
            font-size: 15px;
            letter-spacing: 2.48px;
            color: #ff1744;
            background-color: #fff;
            border: none;
            padding: 9px 10px 8px;
            width: 100%;

            &:after{
                content: '›';
                position: relative;
                top: 1px;
                font-size: 20px;
                font-weight: bold;
                line-height: 1;
                margin-left: 18px;
            }
        }
    }
}
