.top-social-area{
    text-align: center;

    .social-list{
        display: flex;
        justify-content: center;

        .list-inner{
            &:not(:last-child){
                margin-right: 30px;
            }
        }
    }

    .twitter-widget{
        @include mq('md'){
            margin: 0 auto;
            width: 700px;
        }
    }
}