.mypage-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #fff;
    padding: 22px 14px 18px 15px;

    @include mq('md'){
        box-sizing: border-box;
        margin: 0 auto;
    }

    &.modi-add-underbar{
        border-bottom: 1px solid #ccc;
    }

    .mypage-title {
        font-size: 24px;
        letter-spacing: 1.44px;
        color: #010002;
        flex: 1;
    }

    .message-to-select{
        padding: 10px 8px;
        width: 38%;
    }

    .back-to-button {
        text-decoration: none;
        font-size: 13px;
        font-weight: bold;
        line-height: 1.31;
        letter-spacing: 1.56px;
        width: 68px;

        @include mq('md'){
            display: none;
        }

        .back-icon{
            vertical-align: -4px;
            margin-right: 14px;
            width: 16px;
            height: 20px;
        }
    }

    .edit-button {
        text-decoration: none;
        font-size: 15px;
        font-weight: bold;
        letter-spacing: 1.56px;
        width: 94px;

        @include mq('md'){
            display: none;
        }

        .edit-icon{
            vertical-align: -4px;
            margin-right: 10px;
            width: 16px;
            height: 20px;
        }
    }

    .credit-icon-list {
        display: flex;

        .inner-list {
            &:not(:last-child){
                margin-right: 14px;
            }

            .credit-icon{
                width: 37px;
            }
        }
    }
}