.point-contents-wrapper {
    background-color: #efebeb;

    @include mq('md'){
        margin: 0 auto;
        max-width: 939px;
    }

    .point-contents-area {
        padding: 33px 22px 40px;

        .point-list {
            > .list-inner {
                &:not(:last-child){
                    border-bottom: 1px solid #010002;
                    padding-bottom: 38px;
                    margin-bottom: 25px;
                }

                .point-heading {
                    font-size: 16px;
                    letter-spacing: 1.92px;
                    color: #010002;
                    margin-bottom: 20px;
                }

                .point-buy-list {
                    > .list-inner{

                        &:not(:last-child){
                            margin-bottom: 25px;
                        }

                        .point-buy-button {
                            display: flex;
                            justify-content: center;
                            align-items: center;
                            font-family: NotoSansCJKjp;
                            text-decoration: none;
                            color: #ff1744;
                            border: 2px solid #ff1744;
                            background-color: #ffffff;
                            border-radius: 30px;
                            padding: 10px;

                            &.modi-continuously{
                                color: #2cadf4;
                                border: 2px solid #2cadf4;

                                .point-value-text {
                                    border-right: 1px solid #2cadf4;
                                }
                            }

                            .point-value-text {
                                border-right: 1px solid #ff1744;
                                padding-right: 13px;
                                text-align: center;

                                .point-value {
                                    font-family: Roboto;
                                    font-size: 16.45px;
                                    font-weight: 900;
                                    font-style: italic;
                                    line-height: 1.2;
                                    letter-spacing: 1.645px;
                                    margin-bottom: 2px;
                                }

                                .point-add-text {
                                    font-size: 8.7px;
                                    line-height: 1.2;
                                    letter-spacing: 0.87px;
                                }
                            }

                            .point-money-text {
                                display: flex;
                                align-items: flex-end;
                                padding-left: 23px;
                                min-width: 92px;

                                .money-text {
                                    font-family: Roboto;
                                    font-size: 19.4px;
                                    font-weight: 900;
                                    font-style: italic;
                                    letter-spacing: 1.94px;
                                }

                                .money-add-text {
                                    font-size: 7.75px;
                                    letter-spacing: 0.775px;
                                }
                            }

                            .add-icon {
                                margin-left: 5px;
                                width: 17px;
                            }
                        }
                    }
                }
            }
        }
    }

    .point-description-area {
        display: flex;
        align-items: flex-start;
        background-color: #fff;
        padding: 38px 22px 60px;

        .description-icon {
            margin-right: 28px;
            width: 40px;
        }

        .description-text {
            font-size: 13px;
            line-height: 1.75;
            letter-spacing: 0.78px;
            color: #010002;
        }
    }

    .point-menu-list {
        background-color: #fff;

        .list-inner {
            &:not(:first-child){
                margin-top: 30px;
            }

            > a{
                display: block;
                font-size: 16px;
                line-height: 1.625;
                letter-spacing: 1.6px;
                color: #010002;
                margin: 0 auto;
                width: fit-content;
            }
        }
    }
}
