.login-page-area{
    background-color: #efebeb;

    @include mq('md'){
        box-sizing: border-box;
        margin: 0 auto;
        padding: 45px 50px 44px;
        max-width: 1280px;
    }

    @include mq('down-md'){
        padding: 25px 20px;
    }

    .login-main-heading {
        font-size: 23px;
        letter-spacing: 3.76px;

        @include mq('md'){
            font-size: 26px;
        }

        @include mq('down-md'){
            font-size: 23px;
        }
    }

    .login-sub-heading {
        font-size: 16px;
        font-weight: bold;
        line-height: 0.97;
        letter-spacing: 3.84px;
    }

    .text-caution-message {
        font-size: 9px;
    }

    .login-form-list {
        .list-inner {
            margin-bottom: 20px;

            .form-input-normal {
                display: block;
                background-color: transparent;
                font-family: Roboto;
                font-size: 12px;
                font-weight: bold;
                font-stretch: condensed;
                line-height: 1.29;
                letter-spacing: 1.44px;
                color: #000;
                border: none;
                border-bottom: 1px solid #000;
                max-width: 550px;
                width: 100%;

                &::placeholder {
                    color: #000;
                }
            }

            .form-checkbox-normal {
                position: relative;
                outline: none;
                background-color: transparent;
                border: 1px solid #000;
                appearance: none;
                margin-right: 4px;
                width: 10px;
                height: 10px;

                &::before{
                    content: '';
                    display: none;
                    position: absolute;
                    left: 1px;
                    top: 1px;
                    border: 1px solid #000;
                    border-width: 1px 1px 0 0;
                    width: 5px;
                    height: 3px;
                    transform: rotate(135deg);
                }

                &:checked{
                    &::before{
                        display: block;
                    }
                }
            }

            .form-checkbox-label{
                font-size: 24px;
                color: #ffffff;
            }
        }
    }

    .login-submit{
        position: relative;
        display: block;
        text-align: center;
        border-radius: 12px;
        font-size: 15px;
        letter-spacing: 2.48px;
        color: #ff1744;
        background-color: #fff;
        border: none;
        padding: 9px 10px 8px;
        max-width: 400px;
        width: 100%;

        &:after{
            content: '›';
            position: relative;
            top: 1px;
            font-size: 20px;
            font-weight: bold;
            line-height: 1;
            margin-left: 18px;
        }
    }
}