.mypage-profile-info {
    border-top: 13px solid #fff;
    background-color: #efebeb;
    padding: 40px 27px;

    .profile-heading{
        font-size: 18px;
        letter-spacing: 1.08px;
        color: #010002;
        margin-bottom: 28px;
    }

    .profile-info-list {
        .inner-list {
            .profile-label {
                font-size: 13px;
                letter-spacing: 0.78px;
                color: #010002;
                margin-bottom: 15px;
            }

            .profile-value {
                font-size: 13px;
                letter-spacing: 0.78px;
                color: #010002;
                margin-bottom: 30px;

                .twitter-button {
                    display: inline-block;
                    border-radius: 8px;
                    background-color: #2cadf4;
                    font-size: 10.2px;
                    letter-spacing: 1.825px;
                    text-decoration: none;
                    color: #ffffff;
                    margin-left: 20px;
                    padding: 6px 12px 5px;
                }

                .apply-button {
                    display: inline-block;
                    border-radius: 8px;
                    background-color: #ff1744;
                    font-size: 10.2px;
                    letter-spacing: 1.825px;
                    text-decoration: none;
                    color: #ffffff;
                    margin-left: 20px;
                    padding: 6px 12px 5px;
                }
            }
        }
    }
}