.top-slide-area {
    position: relative;

    @include mq('md'){
        padding-bottom: 154px;
    }

    @include mq('down-md'){
        padding-bottom: 28%;
    }

    .slide-list {
        .slick-slide{
            @include mq('md'){
                margin-right: 33px;
            }

            @include mq('down-md'){
                margin-right: 30px;

                &:not(.slick-current){
                    margin-top: 16px;
                    width: 228px;
                }

                &.slick-current{
                    width: 260px;
                }
            }
        }

        .inner-list {
            // width: 234px;

            &:not(:last-child){
                // margin-right: 4%;
            }

            &.modi-main {
                // width: 260px;
            }

            > a{
                position: relative;
                display: block;
                overflow: hidden;
                border-radius: 10px;

                > .girl-name{
                    position: absolute;
                    bottom: 30px;
                    left: 0;
                    right: 0;
                    margin: auto;
                    font-family: FjallaOne;
                    font-size: 20px;
                    letter-spacing: 4.51px;
                    text-align: center;
                    text-shadow: 0 0 10px rgba(0,0,0,0.5);
                    color: #ffffff;
                }
            }
        }
    }

    .social-list{
        display: none;
        position: absolute;
        left: 61px;
        bottom: 60px;
        justify-content: center;

        @include mq('md'){
            display: flex;
        }

        .list-inner{
            &:not(:last-child){
                margin-right: 18px;
            }
        }
    }

    .image-this-is-me{
        position: absolute;
        left: 0;
        right: 0;
        margin: auto;

        @include mq('md'){
            bottom: 54px;
            width: 407px;
        }

        @include mq('down-md'){
            bottom: 16%;
            width: 86%;
        }
    }

    .login-signup-button{
        display: none;
        position: absolute;
        right: 60px;
        bottom: 31px;
        width: 107px;
        height: 107px;

        @include mq('md'){
            display: block;
        }
    }
}
