.information-contents-wrapper{
    @include mq('md'){
        margin: 0 auto;
        max-width: 1200px;
    }

    .information-contents-area {
        border: 1px solid #e5e5e5;
        border-width: 1px 0;
        padding: 33px 0 32px;

        .information-contents-list {
            background-color: #efebeb;
            margin: 32px 0 33px;
            padding: 27px 22px 33px;

            .inner-list {
                border-bottom: 1px solid #010002;
                padding-bottom: 25px;

                &:not(:first-child){
                    padding-top: 22px;
                }

                .information-heading {
                    font-family: Roboto;
                    font-size: 14px;
                    font-weight: bold;
                    font-stretch: condensed;
                    line-height: 1.75;
                    letter-spacing: 1.4px;
                    margin-bottom: 10px;
                }

                .information-text {
                    font-size: 12px;
                    line-height: 1.87;
                    letter-spacing: 2.16px;
                }
            }
        }
    }
}