.top-information-area {
    position: relative;
    color: #fff;
    background-color: #ff1744;

    @include mq('md'){
        display: flex;
        margin: 0 auto;
        width: fit-content;
        align-items: center;
        padding: 10px 17px 8px;
        width: 1025px;
    }

    @include mq('down-md'){
        padding: 8% 5% 8% 18%;
    }

    .login-signup-button{
        position: absolute;
        right: 25px;
        top: -26px;
        width: 53px;

        @include mq('md'){
            display: none;
        }
    }

    .information-heading {
        font-family: Roboto;
        font-weight: 300;

        @include mq('md'){
            font-size: 16px;
            letter-spacing: 1.28px;
            padding: 8px 22px 8px 0;
            border-right: 1px solid #fff;
        }

        @include mq('down-md'){
            position: absolute;
            transform: rotateZ(90deg) translate(0px, 10px);
            transform-origin: left top;
            font-size: 30px;
            letter-spacing: 2.4px;
        }
    }

    .information-list {
        margin-left: 25px;

        .inner-list{
            @include mq('md'){
                &:not(:first-child){
                    display: none;
                }
            }

            @include mq('down-md'){
                &:not(:last-child){
                    margin-bottom: 10%;
                }
            }

            > a{
                display: block;
                text-decoration: none;

                @include mq('md'){
                    display: flex;
                }

                .information-date {
                    font-family: Roboto;
                    font-size: 14px;
                    font-weight: bold;
                    font-stretch: condensed;
                    letter-spacing: 1.4px;
                    margin-right: 20px;

                    @include mq('down-md'){
                        margin-bottom: 2%;
                    }
                }

                .information-text {
                    font-size: 12px;
                    line-height: 1.58;
                    letter-spacing: 2.16px;

                    @include mq('md'){
                        text-overflow: ellipsis;
                        white-space: nowrap;
                        overflow: hidden;
                        width: 505px;
                    }
                }
            }
        }
    }

    .all-button{
        display: none;
        position: absolute;
        right: 20px;
        top: 0;
        bottom: 0;
        margin: auto;
        height: 1.7em;
        font-family: Roboto;
        font-size: 16px;
        font-weight: 300;
        font-stretch: condensed;
        line-height: 1.69;
        letter-spacing: 1.92px;
        color: #fafafa;
        text-decoration: none;
        padding-bottom: 1px;
        border-bottom: 1px solid #fff;

        @include mq('md'){
            display: block;
        }
    }
}