.mypage-magazine-contents-header {
    display: flex;
    align-items: center;
    padding: 15px 15px;
    background-color: #fff;
    border-top: 1px solid #e5e5e5;

    .badge-icon {
        margin-right: 10px;
        width: 27px;
    }

    .message-info {
        .user-name {
            font-size: 16.25px;
            letter-spacing: 0.975px;
            color: #010002;
        }

        .message-date {
            font-size: 12.2px;
            letter-spacing: 1.22px;
            color: #78787b;
        }
    }
}