.download-contents-area {
    @include mq('down-md'){
        padding: 0 23px;
    }

    .download-header {
        font-size: 18px;
        letter-spacing: 2.16px;
        color: #010002;

        .download-icon {
            margin-right: 15px;
            width: 20px;
        }
    }

    .download-list {
        display: flex;
        flex-wrap: wrap;
        margin-bottom: -35px;

        .inner-list{
            margin-bottom: 35px;

            @include mq('md'){
                width: 22%;

                &:not(:nth-child(4n)){
                    margin-right: 4%;
                }
            }

            @include mq('down-md'){
                width: 47%;

                &:nth-child(2n-1){
                    margin-right: 6%;
                }
            }

            .download-image-wrapper {
                position: relative;
                margin-bottom: 8px;

                .badge-image{
                    position: absolute;
                    left: 8px;
                    top: 8px;
                    width: 20px;
                }

                .download-link{
                    display: block;
                }

                .favorite-button {
                    position: absolute;
                    right: 8px;
                    bottom: 8px;
                    box-sizing: border-box;
                    border-radius: 50%;
                    background-color: #ff1744;
                    line-height: 9px;
                    padding: 5px;
                    width: 20px;
                    height: 20px;
                }
            }

            .download-title {
                margin-bottom: 6px;

                > a{
                    text-decoration: none;
                    font-size: 12px;
                    line-height: 1.54;
                    letter-spacing: 1.44px;
                    color: #010002;
                }
            }

            .download-like {
                font-size: 10px;
                font-weight: 100;
                font-style: italic;
                letter-spacing: 1.2px;
                color: #010002;
                margin-bottom: 5px;

                .like-icon{
                    margin-right: 7px;
                    width: 11px;
                }
            }

            .download-limited {
                font-size: 10px;
                line-height: 1.85;
                letter-spacing: 1.2px;
                color: #4d4d4d;
            }
        }
    }

    .button-text {
        font-family: Roboto;
        font-size: 16px;
        font-weight: 300;
        font-stretch: condensed;
        line-height: 1.66;
        letter-spacing: 3.84px;
        text-align: center;
        color: #010002;
    }
}