//------------------------------------------------------------------------------------------------
//
//  drawer
//
//------------------------------------------------------------------------------------------------
body{
    &.is-drawer-active{
        overflow: hidden !important;
    }
}

.l-drawer{
    overflow-y: scroll;
    visibility: hidden;
    transition: transform 0.4s cubic-bezier(0.16, 0.68, 0.43, 0.99);
    box-shadow: 0 8px 10px -5px rgba(0,0,0,.2), 0 16px 24px 2px rgba(0,0,0,.14), 0 6px 30px 5px rgba(0,0,0,.12);
    position: fixed;
    top: 0;
    left: 0;
    z-index: 9999;
    background: #fff;
    height: 100%;

    @include mq('md'){
        width: 50%;
    }

    @include mq('down-md'){
        width: 78.125%;
    }

    &.modi-left{
        transform: translate3d(-100%, 0, 0);
    }

    .is-drawer-active &{
        transition-delay: 0s;
        visibility: visible;
        transform: translate3d(0, 0, 0);
        box-shadow: 0 0 21px rgba(0,0,0,0.4);
    }

    .drawer-close{
        display: none;
        position: fixed;
        right: -60px;
        top: 0;
        z-index: 10000;
        box-sizing: border-box;
        font-size: 2rem;
        text-decoration: none;
        text-align: center;
        line-height: 60px;
        background-color: #ddd;
        width: 60px;
        height: 60px;

        .is-drawer-active &{
            display: block;
        }
    }

    .drawer-header {
        display: flex;
        flex-wrap: wrap;
        align-items: flex-end;
        padding: 19px 18px;

        .logo-main {
            margin-right: 11px;
            width: 100px;
        }

        .menu-text {
            font-size: 14px;
            line-height: 1;
            letter-spacing: 1.4px;
            color: #010002;
        }

        .menu-search {
            position: relative;
            margin-top: 18px;
            width: 100%;

            .search-box{
                background-color: #efebeb;
                border: none;
                border-radius: 30px;
                display: flex;
                justify-content: space-between;
                box-sizing: border-box;
                padding: 12px 18px 13px;
                width: 100%;
            }

            .search-button{
                position: absolute;
                right: 10px;
                top: 0;
                bottom: 0;
                border: none;
                background-color: transparent;

                .search-image{
                    padding: 0;
                    width: 18px;
                }
            }
        }
    }

    .drawer-list{
        border-top: solid 1px #e5e5e5;

        .inner-list{
            border-bottom: solid 1px #e5e5e5;

            > a{
                position: relative;
                display: block;
                text-decoration: none;
                font-size: 14px;
                letter-spacing: 2.7px;
                color: #010002;
                padding: 21px 15px;

                &.modi-notifi-count{
                    &:after{
                        content: attr(data-count);
                        position: absolute;
                        left: 23px;
                        top: 16px;
                        border-radius: 50%;
                        background-color: #ff1744;
                        color: #fff;
                        text-align: center;
                        line-height: 18px;
                        font-family: Roboto;
                        font-weight: bold;
                        font-size: 11px;
                        padding: 1px 0 0 1px;
                        width: 16px;
                    }
                }

                .image-icon{
                    margin-right: 13px;

                    &.image-login{
                        width: 22px;
                    }

                    &.image-smile{
                        width: 23px;
                    }

                    &.image-bell{
                        width: 18px;
                    }

                    &.image-cart{
                        width: 20px;
                    }

                    &.image-star{
                        width: 24px;
                    }
                }
            }
        }
    }

    .social-list {
        display: flex;
        justify-content: center;
        padding: 43px 10px 42px;

        .inner-list{
            &:not(:last-child){
                margin-right: 32px;
            }
        }
    }

    .drawer-footer {
        font-family: Roboto;
        font-size: 16px;
        font-weight: bold;
        font-stretch: condensed;
        letter-spacing: 1.92px;
        color: #fff;
        background-color: #010002;
        padding: 8px 16px 7px;
    }
}

.l-drawer-bg{
    transition: opacity 0.4s, visibility 0s ease 0.4s;
    opacity: 0;
    visibility: hidden;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 9998;
    background-color: rgba(0, 0, 0, 0.65);

    .is-drawer-active &{
        transition-delay: 0s;
        visibility: visible;
        opacity: 1;
    }
}
