.mypage-menu {
    @include mq('down-md'){
        padding: 11px 16px 0;
    }

    .menu-header {
        font-family: Roboto;
        font-size: 34px;
        font-weight: 100;
        font-style: italic;
        line-height: 1.75;
        letter-spacing: 2.72px;
        text-align: left;
        color: #ff1744;
        margin-bottom: 22px;
    }

    .mypage-menu-list {
        @include mq('down-md'){
            border-bottom: 1px solid #e5e5e5;
            margin: 0 -16px;
        }

        .inner-list {
            @include mq('down-md'){
                border-top: 1px solid #e5e5e5;
            }

            > a{
                position: relative;
                display: block;
                text-decoration: none;
                font-size: 13px;
                letter-spacing: 0.78px;
                text-align: left;
                color: #4d4d4d;

                @include mq('md'){
                    margin-top: 15px;
                }

                @include mq('down-md'){
                    padding: 22px 26px 23px 16px;

                    &:before{
                        content: '';
                        position: absolute;
                        right: 26px;
                        top: 0;
                        bottom: 0;
                        transform: rotate(45deg);
                        border: 1px solid #ff1744;
                        border-width: 1px 1px 0 0;
                        margin: auto;
                        width: 10px;
                        height: 10px;
                    }

                    &.modi-notifi-count{
                        &:after{
                            content: attr(data-count);
                            display: inline-block;
                            border-radius: 50%;
                            background-color: #ff1744;
                            color: #fff;
                            text-align: center;
                            line-height: 18px;
                            font-family: Roboto;
                            font-weight: bold;
                            font-size: 11px;
                            margin-left: 10px;
                            padding: 2px 1px 0;
                            width: 18px;
                        }
                    }
                }
            }
        }
    }
}