//  Footer
//
//------------------------------------------------------------------------------------------------
.l-footer{
    color: #fff;
    background-color: #000;
    padding: 36px 20px 35px;

    @include mq('md'){
        display: flex;
        justify-content: space-between;
        box-sizing: border-box;
        min-width: 1280px;
    }

    .foot-nav{
        display: none;

        @include mq('md'){
            display: block;
        }

        .list-foot-menu {
            display: flex;

            .list-inner {
                margin-right: 30px;

                > a{
                    text-decoration: none;
                    font-size: 13px;
                    line-height: 2.08;
                    letter-spacing: 1.56px;
                }
            }
        }
    }

    .copyright{
        display: block;
        font-family: Roboto;
        font-size: 16px;
        font-weight: bold;
        font-stretch: condensed;
        text-align: center;
        letter-spacing: 4px;
        text-align: center;
    }
}