.l-mypage{
    @include mq('md'){
        display: flex;
        box-sizing: border-box;
        margin: 0 auto;
        width: 1280px;
    }

    .mypage-menu{
        margin-right: 50px;
        width: 200px;

        @include mq('down-md'){
            display: none;
        }
    }

    .l-mypage-content{
        flex: 1;
    }
}