.mypage-fc-supporter-area {
    background-color: #efebeb;
    border-top: 13px solid #fff;

    .badge-area{
        padding: 23px 22px 28px;

        .badge-header {
            display: flex;
            align-items: flex-start;

            .badge-image {
                margin-right: 23px;
                width: 50px;
            }

            .text-content {
                .badge-heading {
                    font-size: 14px;
                    letter-spacing: 0.6925px;
                    color: #010002;
                    margin-bottom: 15px;
                }

                .badge-description {
                    font-size: 12px;
                    line-height: 1.63;
                    letter-spacing: 0.6px;
                    color: #010002;
                    margin-bottom: 10px;
                }

                .badge-point {
                    font-size: 10px;
                    line-height: 2.35;
                    letter-spacing: 0.895px;
                }

                .badge-supporter {
                    font-size: 10px;
                    line-height: 2.35;
                    letter-spacing: 0.895px;
                }
            }
        }
    }
}