.faq-contents-area {
    padding: 30px 22px 60px;

    .faq-main-header {
        &.is-close{
            > a{
                &:after{
                    transform: rotate(-45deg);
                    top: 0;
                }
            }

            & + .faq-contents-list{
                display: none;
            }
        }

        display: block;
        position: relative;
        text-decoration: none;
        font-size: 18px;
        letter-spacing: 1.08px;
        text-align: left;
        color: #010002;

        &:after{
            content: '';
            transform: rotate(135deg);
            position: absolute;
            right: 20px;
            top: -9px;
            bottom: 0;
            border: 1px solid #000;
            border-width: 1px 1px 0 0;
            width: 12px;
            height: 12px;
            margin: auto;
        }
    }

    .faq-contents-list {
        .inner-list {
            overflow: hidden;
            border-radius: 10px;

            &:not(:last-child){
                margin-bottom: 25px;
            }

            .faq-header {
                position: relative;
                display: block;
                text-decoration: none;
                background-color: #000;
                font-size: 14px;
                letter-spacing: 0.84px;
                text-align: left;
                color: #ffffff;
                padding: 10px 45px 10px 15px;

                &:after{
                    content: '';
                    transform: rotate(135deg);
                    position: absolute;
                    right: 20px;
                    top: -9px;
                    bottom: 0;
                    border: 1px solid #fff;
                    border-width: 1px 1px 0 0;
                    width: 12px;
                    height: 12px;
                    margin: auto;
                }

                &.is-close{
                    .faq-header{
                        > a{
                            &:after{
                                transform: rotate(-45deg);
                                top: 8px;
                            }
                        }
                    }

                    & + .faq-main-text{
                        display: none;
                    }
                }
            }

            .faq-main-text {
                background-color: #fff;
                font-size: 12px;
                line-height: 1.87;
                letter-spacing: 0.72px;
                text-align: left;
                color: #4d4d4d;
                padding: 15px 19px 18px;
            }
        }
    }
}