.mypage-profile-form-edit {
    border-top: 13px solid #fff;
    background-color: #efebeb;
    padding: 40px 27px;

    .profile-form-list {
        .inner-list {
            .profile-label {
                font-size: 13px;
                letter-spacing: 0.78px;
                color: #010002;
                margin-bottom: 6px;
            }

            .profile-value {
                font-size: 13px;
                letter-spacing: 0.78px;
                color: #010002;
                margin-bottom: 30px;

                .twitter-button {
                    display: inline-block;
                    border-radius: 8px;
                    background-color: #2cadf4;
                    font-size: 10.2px;
                    letter-spacing: 1.825px;
                    text-decoration: none;
                    color: #ffffff;
                    margin-left: 20px;
                    padding: 6px 12px 5px;
                }

                .apply-button {
                    display: inline-block;
                    border-radius: 8px;
                    background-color: #ff1744;
                    font-size: 10.2px;
                    letter-spacing: 1.825px;
                    text-decoration: none;
                    color: #ffffff;
                    margin-left: 20px;
                    padding: 6px 12px 5px;
                }
            }
        }
    }

    .area-button{
        display: flex;
        justify-content: space-between;

        .button-normal{
            @include mq('md'){
                width: 40%;
            }

            &:not(:last-child){
                margin-right: 30px;
            }
        }
    }
}