.mypage-message-contents {
    position: relative;
    background-color: #efebeb;
    padding: 20px 15px 65px;
    min-height: 400px;

    .message-chat-list {
        .inner-list {
            display: flex;
            flex-wrap: wrap;
            align-items: flex-start;

            &:not(:last-child){
                margin-bottom: 30px;
            }

            .user-name{
                margin-bottom: 8px;
                width: 100%;
            }

            .user-icon {
                border-radius: 50%;
                margin-right: 12px;
                width: 38px;
            }

            .message-contents {
                flex: 1;
                padding-left: 20px;

                .message-balloon {
                    position: relative;
                    border-radius: 20px;
                    background-color: #fff;
                    padding: 10px 20px;
                    font-size: 14.7px;
                    letter-spacing: 0.88px;
                    color: #010002;
                    width: fit-content;

                    &:before{
                        content: '';
                        @include arrow(15px, 8px, 'left', #fff);
                        position: absolute;
                        left: -15px;
                        top: 15px;
                    }
                }

                .message-file {
                    margin-top: 7px;

                    .image-file{
                        border-radius: 20px;
                    }
                }

                .message-already-read{
                    margin-top: 7px;
                }

                .message-date {
                    margin-top: 10px;
                }
            }
        }
    }

    .link-report{
        position: absolute;
        bottom: 17px;
        left: 15px;
    }
}
