.mypage-fc-magazine-box-area {
    background-color: #fff;
    border-top: 1px solid #e5e5e5;

    .fc-magazine-box-list {
        .inner-list {
            border-bottom: solid 1px #e5e5e5;

            .message-link {
                text-decoration: none;
                position: relative;
                display: flex;
                align-items: center;
                padding: 12px 15px;

                &:after{
                    content: '';
                    position: absolute;
                    right: 26px;
                    top: 0;
                    bottom: 0;
                    transform: rotate(45deg);
                    border: 1px solid #ff1744;
                    border-width: 1px 1px 0 0;
                    margin: auto;
                    width: 10px;
                    height: 10px;
                }

                .badge-icon {
                    margin-right: 14px;
                    width: 30px;
                }

                .message-info {
                    .user-name {
                        font-size: 16.25px;
                        letter-spacing: 0.975px;
                        color: #010002;
                    }

                    .message-date {
                        font-size: 12.2px;
                        letter-spacing: 1.22px;
                        color: #78787b;
                    }
                }
            }
        }
    }
}