.photo-album-area {
    @include mq('down-md'){
        padding: 0 23px;
    }

    &.modi-mypage{
        background-color: #efebeb;

        @include mq('md'){
            padding: 44px 72px 68px 77px;
        }

        @include mq('down-md'){
            padding: 20px 23px;
        }
    }

    .album-header {
        font-size: 18px;
        line-height: 2.22;
        letter-spacing: 1.08px;
        color: #010002;

        .album-icon {
            margin-right: 14px;
            width: 20px;
        }
    }

    .album-photo-list {
        display: flex;
        flex-wrap: wrap;

        @include mq('down-md'){
            justify-content: space-around;
        }

        .inner-list{
            margin-bottom: 35px;

            @include mq('md'){
                width: 21.4%;

                &:not(:nth-child(4n)){
                    margin-right: 4.79%;
                }
            }

            .photo-image-wrapper{
                position: relative;
                margin-bottom: 7px;

                .badge-image{
                    position: absolute;
                    left: 8px;
                    top: 8px;
                    z-index: 10;
                    width: 20px;
                }

                .album-link{
                    position: relative;
                    display: block;

                    @include mq('md'){
                        &:hover{
                            .text-content{
                                display: flex;
                            }
                        }
                    }

                    .photo-image{

                    }

                    .text-content{
                        display: none;
                        position: absolute;
                        left: 0;
                        right: 0;
                        top: 0;
                        bottom: 0;
                        flex-direction: column;
                        justify-content: flex-end;
                        background-color: rgba(0,0,0,0.7);
                        color: #fff;
                        line-height: 1.8;
                        padding: 10px;
                    }
                }

                .favorite-button {
                    position: absolute;
                    right: 8px;
                    bottom: 8px;
                    z-index: 10;
                    box-sizing: border-box;
                    border-radius: 50%;
                    background-color: #ff1744;
                    line-height: 9px;
                    padding: 5px;
                    width: 20px;
                    height: 20px;
                }

                .trash-button{
                    position: absolute;
                    right: 56px;
                    bottom: 8px;
                    z-index: 10;
                    box-sizing: border-box;
                    border-radius: 50%;
                    background-color: #fff;
                    line-height: 9px;
                    padding: 8px 9px 9px 11px;
                    width: 37px;
                    height: 37px;
                }

                .edit-button{
                    position: absolute;
                    right: 8px;
                    bottom: 8px;
                    z-index: 10;
                    box-sizing: border-box;
                    border-radius: 50%;
                    background-color: #fff;
                    line-height: 9px;
                    padding: 9px;
                    width: 37px;
                    height: 37px;
                }
            }

            .photo-title {
                font-family: HiraKakuProN-W6;
                font-size: 12px;
                line-height: 1.54;
                letter-spacing: 0.72px;
                color: #010002;
                margin-bottom: 7px;
            }

            .photo-like {
                font-family: Roboto;
                font-size: 10px;
                font-weight: 100;
                font-style: italic;
                letter-spacing: 0.6px;
                color: #010002;
                margin-bottom: 5px;

                .like-icon {
                    margin-right: 7px;
                    width: 11px;
                }
            }

            .photo-limited {
                font-size: 10px;
                line-height: 1.85;
                letter-spacing: 0.6px;
                color: #4d4d4d;
            }
        }
    }
}