.mypage-profile-point-area {
    background-color: #efebeb;
    padding: 26px 23px 39px;

    .user-info-wrapper {
        display: flex;
        justify-content: space-between;

        .photo-wrapper {
            margin-right: 30px;

            .user-photo {
                border-radius: 50%;
            }

            .change-text{
                margin-top: 10px;
                text-align: center;

                > a{
                    font-size: 12px;
                    letter-spacing: 0.72px;
                    color: #010002;
                }
            }
        }

        .text-content-wrapper {
            flex: 1;

            .user-name {
                font-weight: bold;
                font-size: 16px;
                letter-spacing: 0.96px;
                color: #010002;
                margin-bottom: 20px;
            }

            .user-heading {
                font-size: 13px;
                letter-spacing: 0.78px;
                color: #010002;
                margin-bottom: 10px;
            }

            .user-has-point {
                font-weight: bold;
                margin-bottom: 25px;

                .volume {
                    font-size: 18px;
                    color: #010002;
                }
            }

            .expire-date-text {
                font-size: 12px;
                letter-spacing: 1.2px;
                color: #78787b;
                margin-bottom: 10px;
            }
        }
    }

    .button-normal {
        border: solid 1px #ff1744;
        color: #ff1744;
        margin-top: 25px;
    }
}