.mypage-form-edit {
    background-color: #efebeb;
    padding: 30px 22px;

    .form-list {
        .inner-list {
            margin-bottom: 25px;

            .form-label {
                display: block;
                font-size: 12px;
                letter-spacing: 1.44px;
                margin-bottom: 8px;
            }

            .form-value {
                .form-input-normal {
                    box-sizing: border-box;
                    border-radius: 4px;
                    background-color: #ffffff;
                    font-size: 13px;
                    border: none;
                    padding: 8px;
                    width: 100%;

                    &[readonly]{
                        background-color: #989898;
                    }
                }

                .form-select{
                    box-sizing: border-box;
                    border-radius: 4px;
                    background-color: #ffffff;
                    font-size: 13px;
                    border: none;
                    padding: 8px;

                    .select-add-text + &{
                        margin-left: 5px;
                    }

                    &.modi-full{
                        width: 100%;
                    }
                }

                .select-add-text{
                    margin-left: 5px;
                }

                .form-textarea {
                    box-sizing: border-box;
                    border-radius: 4px;
                    background-color: #ffffff;
                    font-size: 13px;
                    border: none;
                    padding: 8px;
                    width: 100%;
                }

                .caution-text{
                    margin-top: 5px;
                    font-size: 10px;
                    letter-spacing: 1.08px;
                    color: #989898;
                }

                .form-checkbox-list {
                    display: flex;
                    flex-wrap: wrap;

                    .inner-list {
                        margin-right: 10px;

                        .check-label {
                            font-size: 13px;
                            letter-spacing: 0.78px;
                            color: #010002;

                            .form-checkbox {
                                margin-right: 6px;
                                width: 20px;
                                height: 20px;
                                vertical-align: -5px;
                            }
                        }
                    }
                }
            }

            .form-radio-area {
                margin-top: 10px;

                .radio-label{
                    margin-right: 10px;
                }
            }
        }
    }

    .area-button {
        @include mq('md'){
            display: flex;
            justify-content: space-between;
        }

        .button-normal{
            @include mq('md'){
                &:not(:last-child){
                    margin-right: 30px;
                }
            }

            @include mq('down-md'){
                &:not(:last-child){
                    margin-bottom: 30px;
                }
            }
        }
    }
}