//
//  modal
//
//------------------------------------------------------------------------------------------------
.l-modal{
    display: none;
    box-shadow: 0 8px 10px -5px rgba(0,0,0,.2), 0 16px 24px 2px rgba(0,0,0,.14), 0 6px 30px 5px rgba(0,0,0,.12);
    position: absolute;
    z-index: 9999;
    box-sizing: border-box;
    border-radius: 10px;
    background: #fff;
    margin: auto;
    max-width: 940px;

    .is-modal-show &{
        display: block;
    }

    @include mq('md'){
        top: 50px;
        left: 0;
        right: 0;
        padding: 35px 34px 40px;
        max-height: 80%;
    }

    @include mq('down-md'){
        top: 10px;
        left: 10px;
        right: 10px;
        padding: 25px 24px 20px;
    }

    .modal-close{
        position: absolute;
        z-index: 100;
        text-align: center;
        text-decoration: none;
        background-color: #010002;
        color: #fff;
        border-radius: 50%;
        line-height: 30px;
        width: 30px;
        height: 30px;

        @include mq('md'){
            right: -10px;
            top: -10px;
        }

        @include mq('down-md'){
            right: -8px;
            top: -8px;
        }
    }

    .profile-header {
        display: flex;
        align-items: center;

        .profile-image {
            border-radius: 50%;
            margin-right: 36px;
        }

        .profile-name {
            font-family: Roboto;
            font-size: 20px;
            font-weight: bold;
            font-stretch: condensed;
            letter-spacing: 2px;
            color: #010002;
        }
    }

    .profile-info-list {
        margin-top: 32px;

        .inner-list {
            display: flex;

            .profile-label {
                font-family: HiraKakuProN-W3;
                font-size: 13px;
                line-height: 3.15;
                letter-spacing: 0.78px;
                color: #010002;
                margin-right: 35px;
                width: 82px;
            }

            .profile-value {
                font-weight: bold;
                font-size: 13px;
                line-height: 3.15;
                letter-spacing: 0.78px;
                color: #010002;
                flex: 1;
            }
        }
    }
}

.l-modal-bg{
    display: none;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 9998;
    cursor: pointer;
    background-color: rgba(0, 0, 0, 0.65);

    .is-modal-show &{
        display: block;
    }
}