.c-breadcrumb{
    padding: 10px 16px 20px;

    > li{
        display: inline;

        &:not(:last-child){
            &:after{
                content: ">";
                margin: 0 5px;
            }
        }

        > a{
            font-family: NotoSansCJKjp;
            font-size: 12px;
            letter-spacing: 1.2px;
        }
    }
}
