.mypage-message-box-area {
    background-color: #fff;
    border-top: 1px solid #e5e5e5;

    .message-box-list {
        .inner-list {
            .message-link {
                text-decoration: none;
                position: relative;
                display: flex;
                align-items: center;
                padding: 12px 15px;

                &:not(.modi-new){
                    &:after{
                        content: '';
                        position: absolute;
                        right: 26px;
                        top: 0;
                        bottom: 0;
                        transform: rotate(45deg);
                        border: 1px solid #ff1744;
                        border-width: 1px 1px 0 0;
                        margin: auto;
                        width: 10px;
                        height: 10px;
                    }
                }

                &.modi-new {
                    background-color: #c7bcbc;

                    &:after{
                        content: 'NEW';
                        font-family: Roboto;
                        font-size: 10.85px;
                        font-weight: bold;
                        font-stretch: condensed;
                        letter-spacing: 1.085px;
                        position: absolute;
                        right: 12px;
                        top: 0;
                        bottom: 0;
                        box-sizing: border-box;
                        border-radius: 5px;
                        background-color: #ff1744;
                        color: #fff;
                        margin: auto;
                        padding: 5px;
                        width: 35px;
                        height: 23px;
                    }

                    .message-info {
                        .user-name {
                            color: #fff;
                        }

                        .message-date {
                            color: #fff;
                        }
                    }
                }

                .user-icon {
                    border-radius: 50%;
                    margin-right: 14px;
                    width: 50px;
                }

                .message-info {
                    .user-name {
                        font-size: 16.25px;
                        letter-spacing: 0.975px;
                        color: #010002;
                    }

                    .message-date {
                        font-size: 12.2px;
                        letter-spacing: 1.22px;
                        color: #78787b;
                    }
                }

                .badge-icon {
                    position: absolute;
                    right: 57px;
                    top: 0;
                    bottom: 0;
                    margin: auto;
                    width: 25px;
                }
            }
        }
    }
}